@import "../../../assets/styles/variables.scss";
@import "../../../assets/styles/mixins.scss";

.comment {
    border: 1px $font-color solid;
    display: flex;
    flex-direction: column;
    align-items: baseline;
    justify-content: start;
    border-radius: 5px;
    padding: 7px;
    margin-bottom: 10px;
    &_header {
        text-align: justify;
        font-style: italic;
        font-weight: bold;
        font-size: small;
        background-color: $hover-background;
        border-radius: 5px;
        padding: 2px;
        margin: 3px;
    }
    &_text {
        width: 95%;
        text-align: justify;
        font-size: medium;
        background-color: white;
        padding: 5px;
    }
    &_footer {
        cursor: pointer;
        align-self: center;
        font-weight: bold;
        font-size: medium;
        background-color: $hover-background;
        border-radius: 5px;
        padding: 2px;
        border: 2px solid $hover-background;       
        margin: 3px;
        margin-bottom: 5px;
        &:hover {
            transition-duration: 300ms;
            background-color: $background-color;
            border: 2px solid $hover-background; 
        }
    }
}